<template>
  <div class="p-1">
    <div
      class="p-5 mx-auto mt-10 bg-gray-300 rounded-lg shadow-lg  dark:bg-gray-800 md:w-1/2 bg-opacity-40"
    >
      <div
        class="p-4 my-4 text-xl font-bold text-center text-white bg-green-500  animate-bounce"
        v-if="submitted"
      >
        Question submitted
      </div>
      <form
        ref="form"
        @submit.prevent="submitQuestion"
        class="flex flex-col items-center space-y-4"
      >
        <select
          required
          v-model="question.subject"
          @change="getChapters"
          class="input-block"
          placeholder="Select Subject"
        >
          <option value="">Select a Subject</option>
          <option v-for="(o, i) in subjects" :key="i" :value="o">
            {{ o }}
          </option>
        </select>
        <select v-model="question.chapter" class="input-block">
          <option value="">Select Chapter</option>
          <option v-for="(c, i) in chapters" :key="i" :value="c">
            {{ c }}
          </option>
        </select>
        <input
          type="file"
          class="input-block dark:text-white"
          @change="uploadImage"
          accept="image/*"
        />
        <textarea
          v-model="question.text"
          class="input-block"
          placeholder="Write your question"
        />
        <div>
          <button
            class="flex items-center primary_btn"
            type="submit"
            :disabled="loading"
          >
            <div
              v-if="loading"
              class="w-6 h-6 mx-1 border-4 border-t-4 border-white rounded-full  animate-spin loader_btn"
            />
            {{ loading ? "Processing..." : "Submit Question" }}
          </button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import _ from "lodash";
import { routine } from "../plugins/routine";
export default {
  data() {
    return {
      question: {
        subject: "",
        chapter: "",
        text: "",
        images: [],
        time: new Date().toLocaleString(),
      },
      allsubjects: [],
      subjects: [],
      chapters: [],
      subject: "",
      chapter: "",
      questionData: {},
      loading: false,
      valid: true,
      submitted: false,
      requiredRules: [(v) => !!v || "This field is required"],
    };
  },
  methods: {
    uploadImage(e) {
      e.target.files.forEach((file) => {
        var reader = new FileReader();
        reader.onloadend = () => {
          this.question.images.push(reader.result);
        };
        reader.readAsDataURL(file);
      });
    },
    deleteImage(img) {
      this.question.images.splice(img, 1);
    },
    submitQuestion() {
      if (this.$refs.form.checkValidity()) {
        this.loading = !this.loading;

        let { subject, chapter, text, images, time } = this.question;
        let { name, roll } = JSON.parse(localStorage.getItem("s22ssrb"));
        this.questionData = {
          subject,
          images,
          question: [time, name, roll, subject, chapter, text],
        };

        let url =
          "https://script.google.com/macros/s/AKfycbwY9ggmEmsGViArUyBsZjdOR5HotmJ6MhM9LYHIpJpR53mrmsLmgeCmLBOYHtVoeXw/exec";
        fetch(url, {
          method: "POST",
          mode: "no-cors",
          cache: "no-cache",
          redirect: "follow",
          body: JSON.stringify(this.questionData),
        })
          .then(() => {
            this.loading = !this.loading;
            this.$swal({
              icon: "success",
              title: "Successfully submitted",
            }).then(() => {
              this.$refs.form.reset();
              this.question = {
                subject: "",
                chapter: "",
                text: "",
                images: [],
                time: new Date().toLocaleString(),
              };
              this.subject = "";
              this.chapter = "";
              this.valid = true;
              this.submitted = true;
              setTimeout(() => {
                this.submitted = false;
              }, 3000);
            });
          })
          .catch((e) => console.log(e));
      }
    },
    getSubjects() {
      this.question.chapter = "";

      this.allsubjects = routine.routines;
      this.subjects = _.union(
        this.allsubjects.filter((k) => k.ss22).map((s) => s.subject)
      );
    },
    getChapters() {
      this.chapters = [];
      this.allsubjects.forEach((s) => {
        if (s.subject === this.question.subject) {
          if (!this.chapters.includes(s.chapter)) {
            this.chapters.push(s.chapter);
          }
        }
      });
    },
  },
  created() {
    this.getSubjects();
  },
};
</script>

<style lang="scss" scoped>
.imageContainer {
  position: relative;
}

.delete__button {
  position: absolute;
  top: 0;
  left: 0;
}
.loader_btn {
  border-top-color: rgb(77, 75, 75) !important;
}
</style>